.App {
  text-align: center;
}

.App-logo {
  margin-top: 50px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-pulse infinite 2s linear;
  }
}

.App-header {
  background-color: #b2c9f7;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: darkorchid;
}


@keyframes App-logo-pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scaleY(1);
  }
}

button {
  background:darksalmon;
  font-size: larger;
  padding: 30px;
  margin: 10px;
  opacity: .8;
  transition: opacity 0.5s;
}

button:hover {
  opacity: 1;
}

.numDisplay {
  font-size: 72px;
  margin:20px;
}

.mainWrapper {
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
 
.clearButton {
  background:darkseagreen
}

.explanation {
  padding: 20px;
}